@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);

.header {
  height: 15vh;
  padding: 0.8rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-text {
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
}

.header-text span {
  flex: 1 1;
  height: 3.1rem;
  width: 3.1rem;
  border: 0.06rem solid var(--primary-color);
  background-color: #ffffff;
  color: var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-text span:nth-child(1),
.header-text span:nth-child(6) {
  color: #ffffff;
  background-color: var(--primary-color);
}

@media (min-width: 320px) and (max-width: 480px) {
  .header {
    width: 100%;
    height: 10vh;
  }
  .header-text {
    font-size: 1.4rem;
  }
  .header-text span {
    height: 2rem;
    width: 1.6rem;
  }
}

.quote {
  width: 60vw;
  height: 50vh;
  margin: 0 auto;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  background-color: rgb(125, 17, 40, 0.6);
  text-align: center;
  border-radius: 1rem;
}

.quote-author {
  font-weight: bold;
  text-align: center;
  font-size: 2.1rem;
  color: #00ef00;
}

.quote-text {
  margin-top: 0;
  padding: 0.1rem;
  font-size: 2rem;
  color: #e6e6e6;
}

@media (min-width: 320px) and (max-width: 480px) {
  .quote {
    width: 90vw;
  }
  .quote-text {
    font-size: 1.4rem;
  }
  .quote-author {
    font-size: 1.5rem;
  }
}

.footer {
    height: 15vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    text-align: center;
}

.footer span {
    margin: 0 1.5rem;
    padding: 0.1rem 1rem;
    color: #aaaaaa;
}

.footer a {
    -webkit-text-decoration-color: #ffffff;
            text-decoration-color: #ffffff;
    color: var(--secondary-color);
    font-weight: 500;
}

.footer a:hover {
    text-decoration: none;
}

@media (min-width: 320px) and (max-width: 480px) {
    .footer {
        flex-flow: column;
        align-items: center;
        font-size: 0.9rem;
    }
}
:root {
    --primary-color: #7d1128;
    --secondary-color: #ffffff;
    --twitter-blue: #0c85d0;
    --dark-grey: #5d737e;
}

body {
    padding: 0;
    margin: 0;
    font-family: "Roboto Mono", monospace;
}

.app {
    height: 100vh;
    background-size: cover;
    background-position: center;
}

.button-controls {
    display: flex;
    justify-content: center;
}

@media (min-width: 320px) and (max-width: 480px) {
    .app {
        flex-direction: row;
    }
    .container {
        width: 100vh;
        height: 90vh;
    }
}
.btn {
  color: #ffffff;
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  border-radius: 1.8rem;
  padding: 0.6rem 0;
  min-width: 9rem;
  margin: 1.5rem;
}

.btn-quote {
  color: var(--cralet);
  border: none;
  cursor: pointer;
  background-color: #e7e4e4;
}

.btn-quote:hover {
  background-color: #b1aeae;
}

.btn-tweet {
  display: inline-block;
  background-color: #0b95e0;
}

.btn-tweet:hover {
  background-color: #02588d;
}

.btn-tweet:foucs {
  outline: 0.06rem solid inherit;
}

@media (max-width: 480px) and (min-width: 360px) {
  .btn {
    margin: 0.6rem;
    min-width: 7rem;
  }
}

